@use "global" as *;


.is-fixed {  // ドロワーが開いている際にスクロール出来ないようにするクラス。jsで『html,body』に付与。
  overflow: hidden;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 500;

  background: $color-background-base-2;  // 今だけ指定
}

.header__inner {
  height: rem(96);
  padding-inline: rem(64);
  display: flex;
  justify-content: space-between;
  align-items: center;

  // @include mq(md) {
  //   height: rem(90);
  //   padding-inline: rem(40);
  // }
}

.header__title {
  color: $color-white;
  leading-trim: both;
  text-edge: cap;
  font-family: $font-family-ja-1;
  font-size: rem(16);
  font-weight: $bold;
  line-height: 1.4;
  transition: opacity 0.1s ease-out;
}

.header__title:hover {
  opacity: 0.7;
}

.header__nav {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: rem(48);

  @include mq(md) {
    display: none;
  }
}

.header__link {
  height: 100%;
  color: $color-white;
  leading-trim: both;
  text-edge: cap;
  font-family: $font-family-en-1;
  font-size: rem(20);
  font-weight: $bold;
  line-height: 1.4;
  display: grid;
  place-items: center;
  position: relative;
}

.header__link::after {  // hover時の伸びる下線 (通常は『scaleX(0)』で非表示)
  content: "";
  width: 100%;
  height: 1px;
  background: $color-white;
  position: absolute;
  bottom: rem(30);
  left: 0;
  transform: scaleX(0);
  transform-origin: right bottom;
  transition: transform 0.4s cubic-bezier(0.86, 0, 0.07, 1);
}

.header__link:hover::after {  // hover (下線が伸びる)
  transform: scaleX(1);
  transform-origin: left bottom;
}

.header__btn {
  display: none;

  @include mq(md) {
    width: rem(32);
    height: rem(24);
    position: relative;
    z-index: 100;
    transition: width 0.3s ease-out;
  }
}

.header__btn.is-clicked {
  @include mq(md) {
    width: rem(40);
  }
}

.header__btn.is-clicked .header__bar:nth-of-type(1),
.header__btn.is-clicked .header__bar:nth-of-type(3) {
  @include mq(md) {
    top: rem(12);
  }
}

.header__btn.is-clicked .header__bar:nth-of-type(1) {
  @include mq(md) {
    transform: rotate(36.87deg);
  }
}

.header__btn.is-clicked .header__bar:nth-of-type(2) {
  @include mq(md) {
    opacity: 0;
  }
}

.header__btn.is-clicked .header__bar:nth-of-type(3) {
  @include mq(md) {
    transform: rotate(-36.87deg);
  }
}


.header__bar {
  @include mq(md) {
    display: inline-block;
    width: 100%;
    height: rem(1);
    border-radius: rem(6);
    // background: $color-background-base;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    transition: transform 0.3s ease-out, top 0.3s ease-out, opacity 0.2s ease-out;
  }
}

.header__bar:nth-of-type(2) {
  @include mq(md) {
    top: rem(12);
  }
}

.header__bar:nth-of-type(3) {
  @include mq(md) {
    top: rem(24);
  }
}
