@charset "UTF-8";
body {
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 400;
  color: #402E32;
  font-style: normal;
}

/* 構築時のみ、管理バーを非表示にする */
#wpadminbar {
  display: none !important;
}

.u-desktop {
  display: block;
}
@media screen and (max-width: 767px) {
  .u-desktop {
    display: none;
  }
}

.u-mobile {
  display: none;
}
@media screen and (max-width: 767px) {
  .u-mobile {
    display: block;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 1080px) {
  html {
    font-size: 1.4814814815vw;
  }
}
@media screen and (max-width: 767px) {
  html {
    font-size: 16px;
  }
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1, f h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeLegibility;
  line-height: 1.75;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}

.is-fixed {
  overflow: hidden;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 500;
  background: #998676;
}

.header__inner {
  height: 6rem;
  padding-inline: 4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__title {
  color: #FFF;
  leading-trim: both;
  text-edge: cap;
  font-family: "Zen Old Mincho", serif;
  font-size: 1rem;
  font-weight: 700;
  line-height: 1.4;
  transition: opacity 0.1s ease-out;
}

.header__title:hover {
  opacity: 0.7;
}

.header__nav {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3rem;
}
@media screen and (max-width: 767px) {
  .header__nav {
    display: none;
  }
}

.header__link {
  height: 100%;
  color: #FFF;
  leading-trim: both;
  text-edge: cap;
  font-family: "Cormorant Garamond", serif;
  font-size: 1.25rem;
  font-weight: 700;
  line-height: 1.4;
  display: grid;
  place-items: center;
  position: relative;
}

.header__link::after {
  content: "";
  width: 100%;
  height: 1px;
  background: #FFF;
  position: absolute;
  bottom: 1.875rem;
  left: 0;
  transform: scaleX(0);
  transform-origin: right bottom;
  transition: transform 0.4s cubic-bezier(0.86, 0, 0.07, 1);
}

.header__link:hover::after {
  transform: scaleX(1);
  transform-origin: left bottom;
}

.header__btn {
  display: none;
}
@media screen and (max-width: 767px) {
  .header__btn {
    width: 2rem;
    height: 1.5rem;
    position: relative;
    z-index: 100;
    transition: width 0.3s ease-out;
  }
}

@media screen and (max-width: 767px) {
  .header__btn.is-clicked {
    width: 2.5rem;
  }
}

@media screen and (max-width: 767px) {
  .header__btn.is-clicked .header__bar:nth-of-type(1),
  .header__btn.is-clicked .header__bar:nth-of-type(3) {
    top: 0.75rem;
  }
}

@media screen and (max-width: 767px) {
  .header__btn.is-clicked .header__bar:nth-of-type(1) {
    transform: rotate(36.87deg);
  }
}

@media screen and (max-width: 767px) {
  .header__btn.is-clicked .header__bar:nth-of-type(2) {
    opacity: 0;
  }
}

@media screen and (max-width: 767px) {
  .header__btn.is-clicked .header__bar:nth-of-type(3) {
    transform: rotate(-36.87deg);
  }
}

@media screen and (max-width: 767px) {
  .header__bar {
    display: inline-block;
    width: 100%;
    height: 0.0625rem;
    border-radius: 0.375rem;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    transition: transform 0.3s ease-out, top 0.3s ease-out, opacity 0.2s ease-out;
  }
}

@media screen and (max-width: 767px) {
  .header__bar:nth-of-type(2) {
    top: 0.75rem;
  }
}

@media screen and (max-width: 767px) {
  .header__bar:nth-of-type(3) {
    top: 1.5rem;
  }
}

.inner {
  max-width: 1140px;
  padding-right: 30px;
  padding-left: 30px;
  width: 100%;
  margin-right: auto;
  margin-left: auto;
}
@media screen and (max-width: 767px) {
  .inner {
    padding-right: 24.5px;
    padding-left: 24.5px;
  }
}